                            points[0] = nodeRect.points[0];
                            points[1] = [nowrightBottomPosition[0], nodeRect.points[1][1]];
                            points[2] = [nowrightBottomPosition[0], nowrightBottomPosition[1]];
                            points[3] = [nodeRect.points[3][0], nowrightBottomPosition[1]];
